import React ,{useState}from 'react'

export default function TextForm(props) {
const capitalizeText = (text)=>{
const ourText = text.split(' ');
for(let i=0;i<ourText.length;i++)
{
    ourText[i]= ourText[i].charAt(0).toUpperCase()+ourText[i].slice(1);
}
return ourText.join(' ');
}
const handleUpperClick=()=>{
    // console.log('Clicked'+text);
    let newText = text.toUpperCase();
    setText(newText);
    props.showAlert("uppercase is enabled","success");
}
const handleCapitalizeClick=()=>{
    // console.log('Clicked'+text);
    let newText = capitalizeText(text);
    setText(newText);
    props.showAlert("capitalize is enabled","success");
}
const handleLowerClick=()=>{
    // console.log('Clicked'+text);
    let newText = text.toLowerCase();
    setText(newText);
    props.showAlert("lowercase is enabled","success");
}
const handleClickCopy =()=>{
    var text = document.getElementById('text-area');
    text.select();
    navigator.clipboard.writeText(text.value);
    props.showAlert("click to copy is enabled","success");
}
const handleClearText=()=>{
    setText(" ");
    props.showAlert("Clear text is enabled","success");
}
const handleExtraSpace=()=>{
    // console.log('Content Chnaged');
    let newText = text.split(/[ ]+/);
    newText = newText.join(' ');
    setText(newText);
    props.showAlert("remove extra space is enabled","success");
}
const handleOnChange=(e)=>{
    // console.log('Content Chnaged');
    setText(e.target.value);
    
}


const [text,setText] = useState('');

return (
<>
<div className='container'>
<div className="form-group">
    <label htmlFor="text-area"><h2>{props.heading}</h2></label>
<textarea className="form-control"  onChange={handleOnChange}  rows="7" placeholder='Enter your text here...' id="text-area" value={text}></textarea>
</div>
<div className="row  ">
<div className="col m-2 ">
<button className="btn btn-primary  " onClick={handleUpperClick}>Uppercase</button>
</div>
<div className="col m-2 ">
<button className="btn btn-primary  " onClick={handleCapitalizeClick}>Capitalize</button>
</div>
<div className="col m-2 ">
<button className="btn btn-primary "onClick={handleLowerClick}>Lowercase</button>
</div>
<div className="col m-2 ">
<button className="btn btn-primary " onClick={handleClickCopy}>Click to Copy</button>

</div>
<div className="col m-2 ">
<button className="btn btn-primary " onClick={handleClearText}>Click to Clear</button>
</div>
<div className="col m-2 ">
<button className="btn btn-primary  " onClick={handleExtraSpace}>Remove Extra Space</button>
</div>
</div>

</div>
<div className="container my-3">
    <h2>Your text summary...</h2>
    <p>{text.split(' ').filter((el)=>{return el.length!==0;}).length} Words and {text.length} Characters</p>
    <p>{0.008 * text.split(' ').filter((el)=>{return el.length!==0;}).length} Minutes to Read</p>
    <div className="form-group">
    <input type="range" value={0.008 * text.split(' ').filter((el)=>{return el.length!==0;}).length*10} className="form-control-range" name="range" id="range" />
</div>
    <h2>Preview ...</h2>
    <p>{text}</p>


</div>


</>

  )
}
