import React ,{useState}from 'react'
import About from "./components/About";
import Alert from './components/Alert';
import Navbar from "./components/Navbar";
import TextForm from "./components/TextForm";
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
function App() {

  const [mode,setMode] = useState('light');
  const toggleMode =()=>{
    if(mode === 'light')
    {
      setMode('dark');
      document.body.style.backgroundColor='#042743';
      document.body.style.color='white';
      document.title="TextUtilsAgain-Dark Mode !!! ";
      setInterval(() => {
        document.title="Install TextUtilsAgain Now!!! ";
      }, 1500);
      setInterval(() => {
        document.title="TextUtilsAgain is amazing try once!!!";
      }, 2000);
      showAlert('Dark Mode is enabled !!!','success');
    }else{
      setMode('light');
      document.body.style.backgroundColor='white';
      document.body.style.color='#042743';
      document.title="Light Mode is enabled !!!";
      showAlert('Light Mode is enabled !!!','success');
    }
  }
  const [alert, setAlert] = useState(null);
  const showAlert =(msg,type)=>{
    setAlert({
   msg:msg,
   type:type
    });
    setTimeout(() => {
     setAlert(null);
    }, 3000);
  }



  return (
 
<>
<Router>
   <Navbar title="TextUtilsAgain" about="About Us" mode={mode} toggleMode={toggleMode}/>
  <Alert alert={alert}/>
  <div className="container my-3">
        <Switch>

          <Route exact path="/about">
          <About about="About" />
          </Route>
          <Route exact path="/">
          <TextForm heading="Enter the text to analyze:" showAlert={showAlert} />
          </Route>
        </Switch>
  </div>
 </Router>
</>

  );
}

export default App;
