import React,{useState} from 'react'
import img from '../logo.png'
export default function About(props) {

const [myStyle,setMyStyle] = useState({
    backgroundColor: '#042743',
    color:'white',
});

const [btn,setBtn]=useState('Light Mode');

const toggleStyle=()=>{
    if (myStyle.color === '#042743'){
        setMyStyle({
            backgroundColor: '#042743',
            color:'white',
        });
        setBtn('Light Mode');
    }
    else{
        setMyStyle({
            backgroundColor: 'white',
            color:'#042743',
        });
        setBtn('Dark Mode');
    }
}

  return (
  <>

<button type="button" className="btn btn-outline-info mx-auto d-block" data-toggle="modal" data-target="#exampleModal">
  {props.about}
</button>

<div className="modal fade" id="exampleModal" role="dialog" aria-hidden="true" >
<div className="modal-dialog" role="document">
    <div className="modal-content" style={myStyle}>
      <div className="modal-header">
        <h5 className="modal-title" >Meet the Developer</h5>
        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div className="modal-body">
        <a href="https://anantarajshrestha.com.np"><img className="img-thumbnail" src={img} alt="logo" /></a>
      </div>
      <div className="modal-footer">
        <button type="button" className="btn btn-outline" data-dismiss="modal" style={myStyle}>Close</button>
        <button type="button" className="btn btn-outline" style={myStyle} onClick={toggleStyle}>{btn}</button>
      </div>
    </div>
</div>
</div>

  </>
  )
}
