import React from 'react'
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBell } from '@fortawesome/free-solid-svg-icons'
// import logo from '../logo.png';
import '../App.css';

export default function Navbar(props) {

  return (
    <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
    <Link className="navbar-brand" to="/">{props.title}{/*<img className='img' src={logo} alt="" />*/}</Link>
    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
  
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav mr-auto">
        <li className="nav-item active">
          <Link className="nav-link" to="/">Home <span className="sr-only">(current)</span></Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/about">{props.about}</Link>
        </li>
      </ul>
      <button type="button" className={`btn btn-outline text-${props.mode === 'dark'?'light':'dark'}`}>
      <FontAwesomeIcon icon={faBell}/> <span className="badge">5</span>
</button>
      <div className={`custom-control custom-switch text-${props.mode === 'dark'?'light':'dark'}`}>
  <input type="checkbox" className="custom-control-input" id="customSwitch1"/>
  <label className="custom-control-label" htmlFor="customSwitch1" onClick={props.toggleMode}>Dark Mode</label>
</div>
    </div>
  </nav>
  )
}

Navbar.propTypes = {title:PropTypes.string.isRequired,about:PropTypes.string.isRequired,}
// Navbar.defaultProps = { title:"Dushman Duniya", about:"About Dushman"}
